.back-to-top {
    @include smalltype();

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end;

    .back-to-top__button__arrow {
        background: $color-gray7;
        border-radius: 50%;
        color: $color-purewhite;
        height: $measure--small4;
        margin-left: 0.625rem;
        padding: $measure--half;
        text-align: center;
        width: $measure--small4;

        &:before {
            font-size: 0.625rem;
        }
    }

    &[data-docked='false'] {
        position: absolute;

        a {
            display: inline;
            opacity: 1;
            overflow: hidden;
            position: fixed;
            text-decoration: none;
            z-index: z-index('inpage');
            visibility: visible;

            @include media('<between') {
                bottom: $measure--basic;
                right: $measure--basic;
            }

            @include media('>=between') {
                bottom: $measure--threequarters;
                right: $measure--small1;
            }
        }

        .back-to-top__button__label {
            @include sr-only();
        }

        .back-to-top__button {
            border-radius: 50%;
            display: flex;
            height: $measure--medium3;
            width: $measure--medium3;

            svg {
                display: block;
                position: absolute;
            }

            svg path {
                fill: none;
                stroke: $color-nearblack;
                stroke-width: 5;
                transition: stroke-dashoffset 0ms linear;
            }

            svg circle {
                fill: $color-gray7;
            }
        }

        .back-to-top__button__arrow {
            background: none;
            border-radius: 50%;
            height: 2.556rem;
            margin: auto;
            padding-top: $measure--half;
            position: relative;
            width: 2.556rem;

            &:before {
                font-size: 0.8125rem;
                display: block;
            }
        }

        .back-to-top__button__arrow__label {
            @include font-family('default');
        }
    }

    &[data-docked='true'] {
        a {
            @include anchor-discreet--subelement(
                $color-gray4,
                $color-gray4,
                $color-purewhite,
                $color-purewhite,
                $color-red50,
                'back-to-top__button__label'
            );
        }
    }

    &[hidden='true'] {
        a {
            opacity: 0;
            visibility: hidden;
        }
    }

    a {
        border-bottom: none;
        opacity: 1;
        transition: opacity 1s ease;

        &:active {
            outline: $border-width-default $border-style-dotted $color-gray6;
            background: none;
            border-bottom-width: 0;
        }

        &:focus {
            outline: $border-width-default $border-style-dotted $color-red50;
            border-bottom-width: 0;
        }

        &:hover {
            @include media('hover') {
                color: $color-gray10;
                background: none;
                text-decoration: none;
                border-bottom-width: 0;
            }
        }

        &:hover .back-to-top__button svg circle {
            @include media('hover') {
                fill: $color-gray10;
            }
        }
        &:active .back-to-top__button svg circle {
            fill: $color-gray10;
        }

        &:hover .back-to-top__button__label {
            @include media('hover') {
                color: $color-gray10;
                border-bottom: $border-width-default $border-style-solid
                    $color-gray10;
            }
        }

        &:active .back-to-top__button__label {
            color: $color-gray10;
            border-bottom: $border-width-default $border-style-solid
                $color-gray10;
        }

        & svg {
            display: none;
        }
    }
}

.back-to-top {
    @include media('print') {
        display: none;
    }
}
