///
/// Specify font-family
///
/// @param {String} $family - Font-family value
///
/// @require {Map} $font-families
///
/// @example scss - One family
///   @include font-family(default);
///   // font-family: "Proxima Nova", sans-serif;
///
/// @throw Error if no parameters are passed.
/// @throw Error if parameter passed does not match a key in $font-families.
///
@mixin font-family($family) {
    @if not $family {
        @error "font-family() expects one parameter.";
    }

    @if not map-has-key($font-families, $family) {
        @error "There is no `font-family` named `#{$family}` in `$font-families`. `font-family` should be one of `#{map-keys($font-families)}`.";
    }

    font-family: map-get($font-families, $family);
}

@mixin body-font-features() {
    /**
     * IBM Plex OpenType stylistic sets:
     *
     * ss01: 0/1 double-storey/simple 'a'
     * ss02: 0/1 double-storey/simple 'g'
     * ss03: 0/1 plain/slashed '0'
     * ss04: 0/1 plain/dotted '0'
     */
    font-feature-settings: 'ss01' 0, 'ss02' 1, 'ss03' 1, 'ss04' 0, 'lnum' 1,
        'onum' 0, 'pnum' 1, 'tnum' 0, 'frac' 0, 'liga' 1;

    i,
    em {
        font-feature-settings: 'ss01' 0, 'ss02' 1, 'ss03' 1, 'ss04' 0, 'lnum' 1,
            'onum' 0, 'pnum' 1, 'tnum' 0, 'frac' 0, 'liga' 1;
    }
}

@mixin body-fraction-font-features() {
    /**
     * IBM Plex OpenType stylistic sets:
     *
     * ss01: 0/1 double-storey/simple 'a'
     * ss02: 0/1 double-storey/simple 'g'
     * ss03: 0/1 plain/slashed '0'
     * ss04: 0/1 plain/dotted '0'
     */
    font-feature-settings: 'ss01' 0, 'ss02' 1, 'ss03' 1, 'ss04' 0, 'lnum' 1,
        'onum' 0, 'pnum' 1, 'tnum' 0, 'frac' 1, 'liga' 1;

    i,
    em {
        font-feature-settings: 'ss01' 0, 'ss02' 1, 'ss03' 1, 'ss04' 0, 'lnum' 1,
            'onum' 0, 'pnum' 1, 'tnum' 0, 'frac' 1, 'liga' 1;
    }
}

@mixin heading-font-features() {
    font-feature-settings: 'ss01' 1, 'lnum' 1, 'onum' 0, 'pnum' 1, 'tnum' 0,
        'liga' 1;

    i,
    em {
        font-feature-settings: 'ss01' 0, 'lnum' 1, 'onum' 0, 'pnum' 1, 'tnum' 0,
            'liga' 1;
    }
}

@mixin text-shadow($horizontal: 0.0625rem, $vertical: 0.1875rem, $shadowIntensity: 0.3) {
    text-shadow: 0 $horizontal $vertical rgba(0, 0, 0, $shadowIntensity);
}
