@mixin cta--border(
    $background: transparent,
    $color: $color-purewhite,
    $border: $color-cta--t1,
    $hover-color: $color-purewhite,
    $hover-border: $color-cta-hover--t1,
    $outline: $color-purewhite,
    $corners: square
) {
    @include anchor-reset();
    @include anchor-default-outline($outline);

    background-color: $background;
    border-style: $border-style-solid;
    border-width: $border-width-hairline;
    display: inline-block;
    padding-bottom: $measure--half;
    padding-left: $measure--small2;
    padding-right: $measure--small2;
    padding-top: $measure--half;
    transition: background-color $transition-duration--long,
        border-color $transition-duration--long,
        color $transition-duration--long;

    @if $corners == round {
        border-radius: $measure--quarter;
    }

    &,
    &:focus,
    &:active {
        background-color: $background;
        border-color: $border;
        border-style: $border-style-solid;
        border-width: $border-width-hairline;
        color: $color;
    }

    &:hover {
        @include media('hover') {
            background-color: $hover-border;
            border-color: $hover-border;
            border-style: $border-style-solid;
            border-width: $border-width-hairline;
            color: $hover-color;
        }
    }
}

@mixin cta--border--subelement(
    $background: transparent,
    $color: $color-purewhite,
    $border: $color-cta--t1,
    $outline: $color-purewhite,
    $hover-color: $color-purewhite,
    $hover-border: $color-cta-hover--t1,
    $hover-outline: $color-purewhite,
    $corners: square,
    $class: 'text'
) {
    @include anchor-reset();

    background-color: $background;
    border-style: $border-style-solid;
    border-width: $border-width-hairline;
    display: inline-block;
    padding-bottom: $measure--half;
    padding-left: $measure--small2;
    padding-right: $measure--small2;
    padding-top: $measure--half;
    transition: background-color $transition-duration--long,
        border-color $transition-duration--long,
        color $transition-duration--long;

    @if $corners == round {
        border-radius: $measure--quarter;
    }

    &,
    &:focus,
    &:active {
        background-color: $background;
        border-color: $border;
        border-style: $border-style-solid;
        border-width: $border-width-hairline;
        color: $color;
    }

    &:focus {
        .#{$class} {
            @include outline-properties($outline);
        }
    }

    &:focus:hover {
        .#{$class} {
            outline-color: $hover-outline;
        }
    }

    &:hover {
        @include media('hover') {
            background-color: $hover-border;
            border-color: $hover-border;
            border-style: $border-style-solid;
            border-width: $border-width-hairline;
            color: $hover-color;
        }
    }
}
