////
/// Footer
/// @group patterns
////
.global-footer {
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;

    .global-footer__campuses,
    .global-footer__content__section--contact__content__text,
    .global-footer__useful-links,
    .wrapper--global-footer__content__section--contact__directory {
        a {
            @include anchor-discreet(
                $color-gray4,
                $color-gray4,
                $color-purewhite,
                $color-purewhite,
                $color-red50
            );
        }
    }

    .footer-logo--uol,
    .global-footer__awards {
        a {
            @include anchor-img($color-purewhite);
        }
    }

    .global-footer__content__section--contact__content {
        > a {
            @include anchor-img($color-purewhite);
        }
    }

    .global-footer__social-media {
        a {
            @include anchor-icon(
                $color-purewhite,
                $color-purewhite,
                $color-red50,
                $color-purewhite
            );
        }
    }

    .global-footer__link-ribbon {
        a {
            @include media('<between') {
                @include anchor-discreet--subelement(
                    $color-purewhite,
                    $color-purewhite,
                    $color-purewhite,
                    $color-purewhite,
                    $color-red50,
                    'global-footer__link-ribbon__name'
                );
            }

            @include media('>=between') {
                @include anchor-discreet--notransition--subelement(
                    $color-purewhite,
                    $color-purewhite,
                    $color-purewhite,
                    $color-purewhite,
                    $color-red50,
                    'global-footer__link-ribbon__name__text'
                );
            }
        }
    }

    p {
        @include content-style();

        margin-top: $content-element-margin;
    }

    .global-footer__content__section,
    .global-footer__useful-links,
    .footer-logo--uol,
    .back-to-top[data-docked='true'],
    .copyright,
    hr {
        grid-column-end: span 24;
        grid-column-start: 1;
    }

    hr {
        background-color: transparent;
        border-bottom: 0;
        border-left: 0;
        border-right: 0;
        border-top: $border-width-default solid $color-gray10;
        color: transparent;
        margin-top: $measure--small4;
        width: 100%;
    }

    .global-footer__heading {
        @include h3style();
        @include standard-box();

        margin-top: 0;
        text-align: center;
    }

    .global-footer__tagline {
        @include standfirst();
        @include standard-box();
        text-align: center;
        margin-top: 0;
    }

    .wrapper--global-footer {
        background-color: $color-gray11;
        color: $color-purewhite;
        padding-top: $measure--small4;
        grid-column: 1/-1;
        width: 100vw;
        margin-left: calc(-50vw + 50%);

        @include media('<between') {
            text-align: center;
        }
    }

    .global-footer__content {
        @include standard-box();
        @include x-column-grid(24);

        color: $color-purewhite;
        display: grid;
        padding-bottom: $measure--small4;

        grid-template-rows: repeat(9, auto);

        @include media('>=between') {
            grid-template-rows: repeat(4, auto);
        }
    }

    .global-footer__content__section {
        @include media('>=between') {
            grid-column-end: span 8;
            grid-column-start: auto;
            margin-top: $measure--small4;

            h2 {
                @include h3style();
                margin-top: 0;
            }

            h3 {
                @include h4style();
            }
        }

        h2,
        h3 {
            font-weight: $font-weight-normal;
        }
    }

    .global-footer__content__section--contact__content {
        @include media('>=between') {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
        }

        .global-footer__content__section--contact__content__text {
            p {
                display: inline-block;
                text-align: left;

                @include media('>=between') {
                    display: block;
                }
            }
        }
    }

    .global-footer__campuses {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        list-style: none;
        margin-top: $content-element-margin;

        @include media('<between') {
            justify-content: center;
        }

        li {
            &:after {
                color: $color-gray9;
                content: '|';
                display: inline;
                padding-left: $measure--half;
                padding-right: $measure--half;
                text-align: center;
            }

            &:last-of-type {
                &:after {
                    display: none;
                }
            }
        }
    }

    .wrapper--global-footer__content__section--location__cta,
    .wrapper--global-footer__content__section--contact__directory {
        @include content-style();

        margin-top: $content-element-margin;
        max-width: unset;
    }

    .global-footer__content__section--location__cta {
        @include cta--border(
            $background: $color-red50,
            $color: $color-purewhite,
            $border: $color-red50,
            $hover-color: $color-purewhite,
            $hover-border: $color-red60,
            $outline: $color-purewhite,
            $corners: round
        );
    }

    .global-footer__content__section--location
        .global-footer__content__section--location__cta {
        display: inline-flex;
    }

    .global-footer__content__section--location__address {
        display: inline-block;
        text-align: left;

        @include media('>=between') {
            display: block;
        }
    }

    .global-footer__awards {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        list-style: none;
        margin-top: $heading-margin;
        gap: $measure--small2;

        @include media('>=between') {
            margin-top: 0;
            justify-content: space-between;

            .global-footer__awards__promo {
                flex-basis: 45%;
            }

            li:not(:first-child, :nth-child(2)) {
                flex-basis: 25%;
            }
        }

        @include media('<between') {
            margin-top: $measure--small4;
            justify-content: space-between;

            li {
                flex-basis: 22%;
            }
        }

        @include media('<tablet') {
            margin-top: $measure--small4;
            justify-content: space-around;

            li {
                flex-basis: 20%;
            }
        }

        @include media('<=mobile') {
            li {
                flex-basis: 28%;
            }
        }

        li {
            .logo {
                width: $measure--huge5;
            }

            .logo--7rem {
                width: $measure--large4;
            }

            .logo--ofsted {
                width: $measure--large4;

                @include media('>between') {
                    width: $measure--basic * 4.9;
                }
            }
        }
    }

    .global-footer__link-ribbon {
        @include standard-box();

        list-style: none;
        margin-top: $measure--small2;

        @include media('<between') {
            display: inline-block;
            text-align: left;
        }

        @include media('>=between') {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
        }

        @include media('>=desktop') {
            flex-wrap: nowrap;
        }

        li {
            @include content-style();

            @include media('<between') {
                margin-top: $content-element-margin;
            }

            @include media('>=between') {
                text-align: center;
                width: 25%;
            }

            @include media('>=desktop') {
                width: 12.5%;
            }

            a {
                @include media('>=between') {
                    display: block;
                    height: 100%;
                    padding-bottom: $measure--basic;
                    padding-left: $measure--basic;
                    padding-right: $measure--basic;
                    padding-top: $measure--basic;
                    transition: background-color $transition-duration ease-out;

                    &:hover,
                    &:active {
                        background-color: $color-gray10;
                    }
                }
            }

            .global-footer__link-ribbon__icon {
                color: $color-purewhite;
                display: inline;
                margin-right: $measure--basic;

                @include media('>=between') {
                    @include gammatype();

                    display: block;
                    margin-right: 0;
                    text-align: center;

                    .icon {
                        display: inline-block;
                        font-size: unset;
                    }
                }
            }

            .global-footer__link-ribbon__name {
                margin-top: $content-element-margin;

                @include media('>=between') {
                    display: block;
                }
            }
        }
    }

    .global-footer__social-media {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        list-style: none;
        margin-bottom: $measure--small2;

        @include media('<between') {
            justify-content: center;
        }

        li {
            @include gammatype();

            margin-top: $measure--small2;
            padding-left: $measure--threequarters;
            padding-right: $measure--threequarters;

            @include media('>=between') {
                margin-right: $measure--small2;
                padding-left: 0;
                padding-right: 0;

                &:last-of-type {
                    margin-right: 0;
                }
            }

            a {
                border-color: $color-purewhite;
                border-style: $border-style-solid;
                border-width: $border-width-hairline;
                display: inline-block;
                height: $measure--small4;
                padding: $measure--quarter;
                text-align: center;
                width: $measure--small4;

                .icon {
                    align-items: center;
                    display: flex;
                    height: 100%;
                    justify-content: center;
                    width: 100%;
                }
            }
        }
    }

    .global-footer__social-media__name {
        @include sr-only();
    }

    .global-footer__useful-links {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        list-style: none;
        margin-top: $measure--half;

        @include media('<between') {
            justify-content: center;
        }

        @include media('>=between') {
            grid-column-end: span 16;
            grid-column-start: 1;
            grid-row-start: 4;
        }

        li {
            @include content-style();
            @include smalltype();

            margin-right: $measure--basic;

            &:last-of-type {
                margin-right: 0;
            }
        }
    }

    .footer-logo--uol {
        margin-top: $measure--basic;
        display: flex;
        gap: $measure--small2;
        justify-content: center;
        align-items: center;
        @include media('>=between') {
            grid-column-end: span 8;
            grid-column-start: auto;
            grid-row-start: 4;
        }

        @include media('>=small') {
            grid-column-end: span 8;
            grid-row-end: span 2;
            justify-content: flex-start;
        }

        .logo {
            width: $measure--huge5;
        }
    }

    .copyright {
        @include extrasmalltype();

        @include media('<between') {
            max-width: unset;
        }

        @include media('>=between') {
            grid-column-end: span 16;
            grid-column-start: 1;
            grid-row-start: 5;
        }
    }

    .back-to-top[data-docked='true'] {
        grid-row-start: 9;
        text-align: right;

        @include media('>=between') {
            grid-column-end: span 8;
            grid-column-start: 17;
            grid-row-start: 5;
        }

        @include media('>=small') {
            grid-column-start: 21;
            grid-column-end: span 4;
        }
    }

    @include media('print') {
        display: none;
    }
}
